import React, { ReactElement, ReactNode, useEffect, useRef, useState } from "react";

interface KeepAliveProps {
    activeKey: string; // 当前激活的组件 Key
    children: ReactElement; // 需要缓存的组件
}

// const cache = new Map<string, ReactNode>(); // 组件缓存

const KeepAlive: React.FC<KeepAliveProps> = ({ activeKey, children }) => {
    const cacheRef = useRef(new Map<string, ReactNode>()); // 使用 useRef 存储组件缓存
    const [, forceUpdate] = useState({}); // 用于触发组件更新

    useEffect(() => {
        // 如果缓存中没有当前组件，则存入
        if (!cacheRef.current.has(activeKey)) {
            cacheRef.current.set(activeKey, children);
        }
        forceUpdate({}); // 触发更新
    }, [activeKey, children]);

    return (
        <div>
            {Array.from(cacheRef.current.entries()).map(([key, component]) => (
                <div
                    key={key}
                    style={{ display: key === activeKey ? "block" : "none" }} // 控制显示/隐藏
                >
                    {component}
                </div>
            ))}
        </div>
    );
};

export default KeepAlive;
