import React, { useState } from "react";
import KeepAlive from "./KeepAlive";
import { Button, Space, Switch } from "antd";

// 测试组件 A
const ComponentA = () => {
    const [count, setCount] = useState(0);
    return (
        <div>
            <h2>Component A</h2>
            <p>Count: {count}</p>
            <Button onClick={() => setCount(count + 1)}>+</Button>
        </div>
    );
};

// 测试组件 B
const ComponentB = () => {
    return <h2>Component B</h2>;
};

// Demo 页面
const Demo = () => {
    const [active, setActive] = useState("A");
    const [isKeepAlive, setIsKeepAlive] = useState(true)

    return (
        <div style={{ padding: 20 }}>
            <Space>
                <Switch
                    checkedChildren="开启缓存"
                    unCheckedChildren="关闭缓存"
                    defaultChecked
                    onChange={(check) => {
                        console.log("🚀 ~ Demo ~ check:", check)
                        setIsKeepAlive(check)
                    }}
                />
                <Button type='primary' onClick={() => setActive("A")}>Show A</Button>
                <Button type='primary' onClick={() => setActive("B")}>Show B</Button>

            </Space>

            {isKeepAlive ? (
                <KeepAlive activeKey={active}>
                    {active === "A" ? <ComponentA /> : <ComponentB />}
                </KeepAlive>
            ) : (
                active === "A" ? <ComponentA /> : <ComponentB />
            )}


        </div>
    );
};

export default Demo;
